import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FileSystemResourceService } from 'app/modules/profile/file-system-resource.service';
import { ProfileService } from 'app/modules/profile/profile_service';
import { SocialService } from 'app/modules/profile/social.service';
import { UserprofileService } from 'app/common/user';
import { User } from '../../../common/model/user';
import { UserService } from 'app/common/user/user.service';
import { ModalService } from 'app/common/modal/modal.service';

@Component({

  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.css']
})
export class MyProfileComponent implements OnInit, OnDestroy {

  alive = true;
  loading = true;
  mobile = false;
  banner: any = [];
  profileForm: FormGroup;
  posts: Array<any>;
  columns: Array<any>;
  postFile: any;
  user: User;
  logueado = false;
  sending = false;
  modalLogin = 'ModalLogin';

  opened: boolean = false;
  openedShare: boolean = false;

  currentPage = 0;
  totalPost = 0;
  bannerProfile = 'profile-banner.jpg';

  size: string;

  constructor(
    private profileService: ProfileService,
    private userService: UserService,
    private modalService: ModalService,
    private fb: FormBuilder,
    private userprofileService: UserprofileService,
    private fileSystemResourceService: FileSystemResourceService,
    private socialService: SocialService) {
    this.columns = [{}, {}];
    this.profileForm = fb.group({
      'note': [null, Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(500)])],
    });
  }

  ngOnInit() {
    if (window.screen.width <= 959) {
      this.mobile = true;
    }
    this.getTimeline();
    this.loadSlider();

    this.userService.getUser().takeWhile(() => this.alive).subscribe((user) => {
      if (user && user.token && !user.guest) {
        this.logueado = true;
      }
    });
  }

  images: Array<any>;
  loadSlider() {
    this.getUserProfile();
    // let profile = this.translate.instant("Profile");
    if (this.mobile) {
      this.bannerProfile = 'profile-banner-xs.jpg';
    } else {
      this.bannerProfile = 'profile-banner.jpg';

    }
    let bannerimg = this.banner[0];
    this.images = [
      {
        "sType": "div", "styles": { "width": "100%" }, "content": `
        <div class="container-slide" style="background-image: url('/assets/images/${this.bannerProfile}'); width:100%; background-repeat: no-repeat;background-attachment: initial;background-position: center; background-size:cover;">
        <div class="container-slide">
      <div  class="slide-middle-txt animated">
      <h1>Panda <span>Social</span></h1>
  </div>
  </div>
      ` }];
  }




  saveStory() {
    if (this.sending) {
      return;
    }
    console.log("this.profileForm.value", this.profileForm.value);
    this.sending = true;
    this.opened = false;
    let reloadInfo = () => {
      this.profileForm.patchValue({ note: "" });
      this.profileForm.markAsPristine();
      this.getTimeline();
      delete this.postFile;
      setTimeout(() => {
        this.sending = false;
      }, 1000);

    }
    if (!this.logueado) {
      this.modalService.open(this.modalLogin);
      reloadInfo();
      return;
    }
    this.socialService.saveStory(this.profileForm.value.note).subscribe((res) => {
      if (this.postFile) {
        this.fileSystemResourceService.postUpload(res.directoryCode, this.postFile).subscribe(() => {
          reloadInfo();
        }, _ => {
          reloadInfo();
        })
      } else {
        reloadInfo();
      }
    }, _ => {
      reloadInfo();
    });
  }

  getUserProfile() {
    this.userprofileService.getUserProfile().takeWhile(() => this.alive).subscribe((response) => {
      try {
        this.user = JSON.parse(response.userProfile);
      } catch (error) {

      }

    });
  }

  onScroll() {
    if (this.posts &&  this.posts.length >= this.totalPost) {
      return;
    }
    this.loading = true;
    this.currentPage++;
    this.socialService.getTimeline({
      "page": this.currentPage,
      "sortTypeId": 1,
      "pageSize": 10
    }).takeWhile(() => this.alive)
      .subscribe(res => {
        this.posts.push(...res);
        if (this.posts && this.posts[0]) {
          this.totalPost = this.posts[0].totalCount
        }
        this.loading = false;
      }, err => {
        this.loading = false;
      });
  }

  getTimeline() {
    let currentPage = 0;
    if (this.currentPage > 0) {
      currentPage = this.currentPage - 1;
    }
    this.loading = true;
    this.socialService.getTimeline({
      "page": currentPage,
      "sortTypeId": 1,
      "pageSize": 10
    }).takeWhile(() => this.alive)
      .subscribe(res => {
        this.posts = res;
        if (this.posts && this.posts[0]) {
          this.totalPost = this.posts[0].totalCount
        }
        console.log("total products", this.totalPost)
        console.log("timeline>>> ", res);
        this.loading = false;
      }, err => {
        this.loading = false;
      });
  }

  postImage(fileInput: any) {
    this.postFile = fileInput.target.files[0];
    console.log("postFile",this.postFile);
  }

  open(size?: string) {
    this.size = size;
    this.opened = !this.opened;
  }

  cancel() {
    this.opened = false;
  }

  addPost(image?) {
    this.opened = false;
  }

  openShare(size?: string) {
    this.size = size;
    this.openedShare = !this.openedShare;
  }

  cancelShare() {
    this.openedShare = false;
  }

  loadNotes() {
    this.profileService.loadNotes()
      .takeWhile(() => this.alive)
      .subscribe(() => {
      })
  }

  submitStory() {
    this.saveStory();
  }

  loadPosts() {
    this.profileService.loadPosts()
      .takeWhile(() => this.alive)
      .subscribe((res) => {
        this.posts = res;
      })
  }

  loadComments(post) {
    this.profileService.loadComments(post.id)
      .takeWhile(() => this.alive)
      .subscribe((res) => {
        for (let pos of post) {
          if (pos.id == post.id) {
            this.posts["comments"] = res;
          }
        }

      })
  }

  likePost() {
    this.profileService.likePost(this.profileForm.value.note)
      .takeWhile(() => this.alive)
      .subscribe(() => {
      })
  }

  ngOnDestroy() {
    this.alive = false;
  }

}



